import { Component, OnInit, ViewChild } from '@angular/core';
import { IonSlides} from '@ionic/angular';

@Component({
  selector: 'app-btn2',
  templateUrl: './btn2.component.html',
  styleUrls: ['./btn2.component.scss'],
})
export class Btn2Component implements OnInit {
  @ViewChild('slideBtn2', { static: false }) slideWithNav: IonSlides;
  isFirst =  true;
  isLast = false;
  isPlay = false;
  index = 0;
  
  slideOpts = {
    initialSlide: 0,
    speed: 400,
    autoHeight: true
  };
  constructor() { }

  checkIndexSlide(){
    this.slideWithNav.getActiveIndex().then((index)=>{
      this.slideWithNav.update()
      document.querySelector('ion-content').scrollToTop(500);
      console.log(index);
      this.index = index;
      if(index != 0){
        this.isFirst = false
        index == 7 ? this.isLast = true : this.isLast = false
      }else{
        this.isFirst = true
        this.isLast = false
      }
    })
  }

  next(){
    this.slideWithNav.slideNext();
  }

  prev(){
    this.slideWithNav.slidePrev();
  }

  ngOnInit() {}

}
