import { Component, OnInit, ViewChild } from '@angular/core';
import { IonSlides } from '@ionic/angular';
import { InduccionPage } from 'src/app/pages/induccion/induccion.page';

@Component({
  selector: 'app-intro',
  templateUrl: './intro.component.html',
  styleUrls: ['./intro.component.scss'],
})
export class IntroComponent implements OnInit {
  @ViewChild('slideBtnIntro', { static: false }) slideWithNav: IonSlides;
  isFirst = true;
  isLast = false;
  isPlay = false;
  index = 0;

  slideOpts = {
    initialSlide: 0,
    speed: 400,
    autoHeight: true
  };

  constructor(private induccion: InduccionPage) {}

  checkIndexSlide() {
    this.slideWithNav.getActiveIndex().then((index) => {
      this.slideWithNav.update()
      // document.querySelector('ion-content').scrollToTop(500);
      console.log(index);
      this.index = index;
      if (this.index != 0) {
        this.isFirst = false;
        index == 3 ? (this.isLast = true) : (this.isLast = false);
      } else {
        this.isFirst = true;
        this.isLast = false;
      }
    });
  }

  next() {
    this.slideWithNav.slideNext();
  }

  prev() {
    this.slideWithNav.slidePrev();
  }

  ngOnInit() {}
}
